/* headers */
.dashboard-item-select h3 {
    font-size: 15px;
    color: #444;
}
[dir] .dashboard-item-select h3 {
    margin-top: 12px;
    margin-bottom: 10px;
}

/* list items */
.dashboard-item-select span p {
    font-size: 15px;
}

/* buttons */
.dashboard-item-select button {
    -ms-flex-item-align: end;
        align-self: flex-end;
}

.dashboard-item-select button span {
    font-size: 13px !important;
    font-weight: 700 !important;
    color: #1565c0;
}



