/* react-grid-layout style */

.react-grid-item {
    overflow: hidden !important;
    min-height: 70px;
    min-width: 70px;
}

[dir] .react-grid-item {
    -webkit-box-shadow: 0px 0px 3px 0px #999999;
            box-shadow: 0px 0px 3px 0px #999999;
    background-color: #ffffff;
    border-radius: 3px;
}

.react-grid-item.edit,
.react-grid-item.RESOURCES,
.react-grid-item.TEXT,
.react-grid-item.MESSAGES,
.react-grid-item.APP {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

[dir] .react-grid-item.edit:hover {
    cursor: move;
}

.react-grid-item.SPACER.view {
    visibility: hidden;
}

[dir] .react-grid-item.SPACER.edit {
    background-color: rgba(255, 255, 255, 0.3);
}

.react-grid-item.react-grid-placeholder {
    opacity: 0.1;
    z-index: 2;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
}

[dir] .react-grid-item.react-grid-placeholder {
    background: #000;
    -webkit-transition-duration: 150ms;
         -o-transition-duration: 150ms;
            transition-duration: 150ms;
}

/* react-resizable */

[dir] .react-resizable-handle {
    background: none;
}

/* dashboard item - header */

.dashboard-item-header {
    font-size: 15px;
    font-weight: 700;
    color: #3a3a3a;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    height: 45px;
}

[dir=ltr] .dashboard-item-header {
    padding: 0 4px 0 14px;
}

[dir=rtl] .dashboard-item-header {
    padding: 0 14px 0 4px;
}

.dashboard-item-header-title {
    -ms-flex: 1 1;
        flex: 1 1;
    overflow: hidden;
    position: relative;
    top: -1px;
}

[dir=ltr] .dashboard-item-header-title {
    left: -1px;
}

[dir=rtl] .dashboard-item-header-title {
    right: -1px;
}

/* dashboard item - content */

.dashboard-item-content {
    overflow: auto;
}

[dir] .dashboard-item-content {
    margin: 0 4px 4px;
}

.react-grid-item.edit .dashboard-item-content,
.react-grid-item.RESOURCES .dashboard-item-content,
.react-grid-item.TEXT .dashboard-item-content,
.react-grid-item.MESSAGES .dashboard-item-content,
.react-grid-item.APP .dashboard-item-content {
    -ms-flex: 1 1;
        flex: 1 1;
}

.CHART.resizing .dashboard-item-content,
.EVENT_CHART.resizing .dashboard-item-content {
    overflow: hidden;
}



