.d2-ui-control-bar-dragflap-icon {
    visibility: hidden;
}

.left-controls {
    font-size: 16px;
    height: 36px;
}

[dir=ltr] .left-controls {
    float: left;
}

[dir=rtl] .left-controls {
    float: right;
}

[dir=ltr] .right-controls {
    float: right;
}

[dir=rtl] .right-controls {
    float: left;
}



