.message-item {
    height: 80px;
    overflow: hidden;
}[dir] .message-item {
    border-bottom: 1px solid #eceff1;
    padding-bottom: 10px;
    margin: 0 5px 10px 5px;
}

[dir] .message-item.view:hover {
    cursor: pointer;
}

.message-title {
    font-size: 15px;
    line-height: 18px;
    color: #000000;
}

[dir] .message-title {
    margin: 0 0 5px 0;
}

.message-title.unread {
    font-weight: bold;
}

.message-title.unread::before {
    content: '‚óè';
    color: #48a999;
    position: relative;
    top: -1px;
}

[dir=ltr] .message-title.unread::before {
    margin-right: 5px;
}

[dir=rtl] .message-title.unread::before {
    margin-left: 5px;
}



